/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  border: 0;
  margin: 0;
  outline: 0;
  padding: 0;
  vertical-align: baseline;
}

html {
  -webkit-font-smoothing: none;
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  overflow-x: hidden;
}

body {
  margin: 0;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 14px;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

a:active {
  outline: 0;
}

a,
a:visited,
a:hover,
a:focus,
a:-webkit-any-link {
  text-decoration: none;
}

a {
  color: #0F8B8D;
}

figure {
  margin: 0;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

input[type=search], input[type=text], input[type=password] {
  padding: 8px;
  flex-grow: 1;
  min-width: 3em;
  border: 1px solid #949494;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
}

input[type=submit],
button[type=submit] {
  background: #f7f7f7;
  border: 1px solid #ccc;
  padding: 8px 0.625em;
  color: #32373c;
  margin-left: 0.625em;
  word-break: normal;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  cursor: pointer;
}

.admin-bar .md-storyful-header {
  top: 32px;
}
@media (max-width: 767px) {
  .admin-bar .md-storyful-header {
    top: 46px;
  }
}
.admin-bar .md-storyful-header.fixed-header {
  top: 32px;
}
@media (max-width: 767px) {
  .admin-bar .md-storyful-header.fixed-header {
    top: 0;
  }
}

.md-storyful-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 200;
  transition: all 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53);
  box-shadow: 0 5px 10px 0 rgba(50, 50, 50, 0.08);
}
.md-storyful-header .site-title {
  font-size: 26px;
  font-weight: 700;
}
.md-storyful-header.fixed-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

@media (min-width: 768px) {
  .md-storyful-menu ul {
    position: relative;
    text-align: right;
  }
  .md-storyful-menu ul li {
    position: relative;
    display: inline-block;
    margin-right: 30px;
  }
  .md-storyful-menu ul li a {
    font-size: 15px;
    font-weight: 500;
    line-height: 26px;
    position: relative;
    z-index: 999;
  }
  .md-storyful-menu ul li .children,
  .md-storyful-menu ul li .sub-menu {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
    display: none;
    float: left;
    padding: 0.5rem 0;
    margin: 0.125rem 0 0;
    text-align: left;
    background-clip: padding-box;
    border: 1px solid #e1e1e5;
    background-color: #ffffff;
    margin-top: 0;
    min-width: 15rem;
    box-shadow: 3px 6px 14px 0 rgba(0, 0, 0, 0.25);
  }
  .md-storyful-menu ul li .children li,
  .md-storyful-menu ul li .sub-menu li {
    margin-right: 0;
    display: block;
    padding: 10px;
    border-bottom: 1px solid #e1e1e5;
  }
  .md-storyful-menu ul li .children li:last-child,
  .md-storyful-menu ul li .sub-menu li:last-child {
    border-bottom: none;
  }
  .md-storyful-menu ul li .children li .children,
  .md-storyful-menu ul li .children li .sub-menu,
  .md-storyful-menu ul li .sub-menu li .children,
  .md-storyful-menu ul li .sub-menu li .sub-menu {
    display: none;
  }
  .md-storyful-menu ul li .children li:hover .children,
  .md-storyful-menu ul li .children li:hover .sub-menu,
  .md-storyful-menu ul li .sub-menu li:hover .children,
  .md-storyful-menu ul li .sub-menu li:hover .sub-menu {
    display: block;
